@import url(https://fonts.googleapis.com/css?family=Montserrat:300,700&display=swap);
body {
	background-color: #f0f3f6;
	margin: 0 auto;
	font-family: 'Montserrat', sans-serif;
	width: 100%;
	overflow-x: hidden;
}
.social-profile_profile__3vUni {
	margin: 30px auto 0;
	height: 300px;
	width: 250px;
	background-color: #fff;
	box-shadow: 0px 3px 8px 0px rgba(0, 0, 0, 0.267);
}

.social-profile_profile__3vUni div {
	display: block;
	padding-top: 20px;
}

.social-profile_profile__3vUni img {
	display: block;
	margin: 0 auto;
	border-radius: 50%;
	width: 100px;
}

.social-profile_profile__3vUni p:first-of-type {
	text-align: center;
	font-size: 20px;
	font-weight: 600;
	color: #122236;
	margin: 5px 0;
}

.social-profile_profile__3vUni p {
	display: block;
	text-align: center;
	font-size: 15px;
	font-weight: 400;
	color: #768696;
}

.social-profile_profile__3vUni ul {
	display: flex;
	justify-content: space-around;
	list-style: none;
	padding: 15px 0;
	margin-top: 28px 0 0 0;
	text-align: center;
}

.social-profile_profile__3vUni span:first-of-type {
	display: block;
	text-align: center;
	font-family: 'Lato', sans-serif;
	font-size: 12px;
	font-weight: 400;
	color: #032342;
}

.social-profile_profile__3vUni span {
	font-weight: 900;
	color: #122236;
	margin-top: 4px;
}
.statistics_statistics__1209m {
	display: block;
	width: 270px;
	margin: 30px auto;
	padding-top: 25px;
	background-color: #fff;
	box-shadow: 0px 3px 8px 0px rgba(0, 0, 0, 0.267);
}

.statistics_statistics__1209m h2 {
	margin: 0;
	margin-bottom: 25px;
	font-weight: 500;
	text-align: center;
	text-transform: uppercase;
	color: #475965;
}

.statistics_statistics__1209m ul {
	padding: 0;
	font-size: 14px;
	display: flex;
	justify-content: space-between;
	list-style: none;
	color: #fff;
}

.statistics_statistics__1209m li {
	width: 25%;
	padding: 12px 0 5px 0;
	background-color: azure;
}

.statistics_statistics__1209m span {
	display: block;
	text-align: center;
	margin-bottom: 5px;
}
.friends_friendList__2CTdM {
	position: relative;
	left: 50%;
	transform: translateX(-200px);
	list-style: none;
}

.friends_friendList__2CTdM li {
	width: 320px;
	margin-bottom: 10px;
	padding: 5px 0;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	box-shadow: 0px 3px 8px 0px rgba(0, 0, 0, 0.267);
	background-color: #ffffff;
}

.friends_online__3uji1, .friends_offline__2c3DR {
	display: block;
	width: 22px;
	height: 22px;
	border-radius: 50%;
	margin: 0 25px;
}

.friends_online__3uji1 {
	background-color: rgb(8, 116, 23);
}

.friends_offline__2c3DR {
	background-color: rgb(160, 15, 15);
}

.friends_friendList__2CTdM li img {
	display: block;
	width: 100px;
	height: 100px;
	border-radius: 10px;
}

.friends_friendList__2CTdM li p {
	display: block;
	font-size: 30px;
	margin-left: 20px;
	font-weight: 400;
}

.friends_friendList__2CTdM li p::first-letter {
	text-transform: capitalize;
}
.transaction-history_transactionHistory__36_w3 {
	margin: 30px auto;
	width: 500px;
	border-collapse: collapse;
	background-color: rgba(153, 180, 202, 0.788);
	box-shadow: 0px 3px 8px 0px rgba(0, 0, 0, 0.267);
}

table,
td,
th {
	border: 1px solid rgba(125, 201, 231, 0.315);
}

.transaction-history_transactionHistory__36_w3 th {
	padding: 10px 0;
	font-size: 15px;
	font-weight: 600;
	text-transform: uppercase;
	background-color: #3cb5e99a;
	color: #ffff;
}

.transaction-history_transactionHistory__36_w3 tr {
	font-size: 13px;
	font-weight: 300;
	text-align: center;
	background-color: #fff;
}

.transaction-history_transactionHistory__36_w3 td {
	padding-top: 14px;
	padding-bottom: 14px;
	text-transform: capitalize;
}
